import { AppBar, IconButton, Toolbar, CssBaseline, Drawer, List, ListItem, ListItemText, Typography } from '@mui/material';
import React, { useState } from 'react';
import MenuIcon from '@mui/icons-material/Menu';
import HomeIcon from '@mui/icons-material/Home';
import AttachMoneyIcon from '@mui/icons-material/AttachMoney';
import CallSplitIcon from '@mui/icons-material/CallSplit';
import { Link } from 'react-router-dom';

function Sidebar() {

    const [showMenu, setShowMenu] = useState(false);

    
    return (
        <CssBaseline>
            <AppBar position="static">
                <Toolbar>
                    <IconButton
                        edge="start"
                        color="inherit"
                        onClick={ () => { setShowMenu(true) }}>
                        <MenuIcon>Menu</MenuIcon>
                    </IconButton>

                        <Typography variant="h4" >
                        Simple Cost Splitter
                    </Typography>
                    
                    <Drawer
                        open={showMenu}
                        onClose={ () => { setShowMenu(false) }}>
                        <List
                            onClick={() => { setShowMenu(false) }}
                            style={{
                            width: 200,
                            margin: 25
                        }}>
                            <ListItem button component={Link} to='/'>
                                <HomeIcon/>
                                <ListItemText primary="Home" style={{ margin: 5 }}/>
                            </ListItem>
                            
                            <ListItem button component={Link} to='/costs'>
                                <AttachMoneyIcon />
                                <ListItemText primary="Costs" style={{ margin: 5 }}/>
                            </ListItem>

                            <ListItem button component={Link} to='/split'>
                                <CallSplitIcon/>
                                <ListItemText primary="Split" style={{ margin: 5 }}/>
                            </ListItem>
                        </List>
                    </Drawer>
                </Toolbar>
            </AppBar>
        </CssBaseline>
    )
}

export default Sidebar