import { Container, ListItem, ListItemText, ListSubheader } from '@mui/material'
import React, { useEffect, useState } from 'react'

function Split(props) {
    const [splittedList, setSplittedList] = useState([]);
    let newList = [];
    
    const [totalSpent, setTotalSpent] = useState(0);
    const totalDivided = totalSpent / splittedList.length;

    const updateTotal = (Total) => {
        setTotalSpent(Total)

    }

    const SplittaaList = () => {   //  [...personsList, { name: addPerson, for: addReason, sum: addSum }]
        let dict = {}
        let total = 0;
        for (let i = 0; i < props.listOfCosts.length; i++) {
            dict[props.listOfCosts[i].name] = (Number(dict[props.listOfCosts[i].name] ? Number(props.listOfCosts[i].sum) + Number(dict[props.listOfCosts[i].name]) : Number(props.listOfCosts[i].sum)));

        }
        Object.entries(dict).forEach(([key, value]) => {
            newList.push({ name: key, sum: value })
            total += Number(value)
        });
        updateTotal(total);
        setSplittedList(newList)
    }
    useEffect(() => {
    SplittaaList();
    }, [])


    return (
        <Container style={{ textAlign: 'center' }}>
            {splittedList.map((data, idx) => {
                return (
                    <ListItem style={{ margin: 20, textAlign: 'center' }} key={idx}>
                        <ListSubheader>Who</ListSubheader>
                            <ListItemText>{data.name}</ListItemText>
                        
                        <ListSubheader>Total spent: </ListSubheader>
                            <ListItemText>{data.sum}</ListItemText>

                        <ListSubheader> {data.sum - totalDivided < 0 ? 'I owe money ' : "I'll get money"}: </ListSubheader>
                            <ListItemText>{data.sum - totalDivided}</ListItemText>
                    </ListItem>
                )
            })}
            Total Spent on trip: {totalSpent ? totalSpent : ''}
            <br/>
            Divided with {splittedList.length} people {totalDivided ? totalDivided : ''}
        </Container>

    )
}

export default Split;