import { Button, Container, ListItem, ListItemText, ListSubheader, TextField, Typography } from '@mui/material';
import React, { useState, useRef } from 'react'
import { useNavigate } from 'react-router-dom';
import { MuiPickersUtilsProvider, DateTimePicker } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';

function Costs(props) {

    const navigate = useNavigate();

    const [errors, setErrors] = useState({});

    const formInfo = useRef({});


    const [personsList, setPersonsList] = useState([]);

    const updatePersonsList = () => {
        setPersonsList([...personsList, { name: formInfo.current.name, for: formInfo.current.reason, sum: formInfo.current.sum }])
    }

    const saveAndNavigate = () => {
        props.updateList(personsList)
        navigate('/split');
    }

    const formChecker = (e) => {
        e.preventDefault();
        let tempErrorList = {};

        if (!formInfo.current.name) {
            tempErrorList = { ...tempErrorList, name : "Missing name"}
        }
        if (!formInfo.current.reason) {
            tempErrorList = { ...tempErrorList, reason: "Missing reason" }
        }
        if (!formInfo.current.sum) {
            tempErrorList = { ...tempErrorList, sum: "Missing sum" }
        }
        if (Object.entries(tempErrorList).length > 0) {
            setErrors({ ...tempErrorList })
        } else {
            setErrors({});
            updatePersonsList();
    }
    }



    const inputHandler = (e) => {
        formInfo.current[e.target.name] = e.target.value;
    }


return (
    <div style={{ textAlign: 'center' }}>
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
<Container>
<Typography>
    Input persons:
</Typography>
    <br />
        <Container>
            <form onSubmit={formChecker}>
                    <TextField name="name" required id="outlined-required" label="Person" style={{ margin: 20 }}
                        onChange={inputHandler} error={Boolean(errors.name)} helperText={errors.name} />
                    
                    <TextField name="reason" required id="outlined-required" label="for what" style={{ margin: 20 }}
                        onChange={inputHandler} error={Boolean(errors.reason)} helperText={errors.reason} />
                    
                    <TextField name="sum" required id="outlined-required" label="how much" type="number" style={{ margin: 20 }}
                            onChange={inputHandler} error={Boolean(errors.sum)} helperText={errors.sum} />
                    
                    
                        {/* <DateTimePicker /> Uncaught RangeError: Format string contains an unescaped latin alphabet character `n`
                        ei toimi.... */}
        
        <Button variant='contained' color='primary' style={{ margin: 20 }} type="submit" >Add costs </Button>
        </form>
    </Container>
    
    <br />
    <Typography>
        {personsList.sort().map((info, idx) => {
            return (
                <ListItem style={{ margin: 20, textAlign: 'center' }} key={idx}>
                        <ListSubheader>Who</ListSubheader>
                        <ListItemText>
                        {info.name}
                        </ListItemText>
                        <ListSubheader>For what:</ListSubheader>
                        <ListItemText>{info.for}</ListItemText>
                        <ListSubheader>How much: </ListSubheader>
                        <ListItemText>{info.sum}</ListItemText>
                        )
                </ListItem>
            )
        })
        }
    </Typography>
    <Button variant='contained' color='primary' style={{ margin: 20 }} onClick={saveAndNavigate}> Save costs </Button>
</Container>
</MuiPickersUtilsProvider>
</div>
)
}
export default Costs;