import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import Home from './components/Home.js';
import Sidebar from './components/Sidebar.js';
import Costs from './components/Costs.js';
import Split from './components/Split.js';
import { useEffect, useState } from 'react';

function App() {

  const [listOfCosts, setListOfCosts] = useState([]);
  
  const updateList = (newList) => {
    setListOfCosts(newList);
    saveToFile(newList);
  }


  const saveToFile = (newList) => {
    localStorage.setItem('List', JSON.stringify(newList));
  }

  const loadFromFile = () => {
    setListOfCosts(JSON.parse(localStorage.getItem('List')))

  }

  useEffect(() => {
    loadFromFile();
  }, [])

  return (
    <Router>
    <Sidebar />
      <Routes>
        <Route path='/' exact element={<Home />} />
        <Route path='/costs' element={<Costs updateList={updateList} />} />
        <Route path='/split' element={<Split listOfCosts={listOfCosts}/>} />
      </Routes>
    </Router>
  );
}

export default App;
